import { Costume } from './types';

const costumes = new Map<string, Costume>();

export function get(name: string) {
  const costume = costumes.get(name);
  if (costume) {
    return costume;
  } else {
    throw new Error('invalid costume name: ' + name);
  }
}

let nextId = 1;

export function add(element: HTMLCanvasElement | HTMLImageElement) {
  const id = `costume_${nextId++}`;
  costumes.set(id, costumeFromElement(element));
  return id;
}

export function loadSampleCostumes() {
  costumes.clear();
  costumes.set('bear', costumeFromElementId('bear'));
  costumes.set('penguin', costumeFromElementId('penguin'));
  costumes.set('blosh', costumeFromElementId('blosh'));
  costumes.set('ball', costumeFromElementId('ball'));
  createCostume(100, 100, (ctx) => {
      ctx.fillStyle = 'red';
      ctx.beginPath();
      ctx.fillRect(0, 0, 100, 100);
      ctx.fill();
    }, 'red square');
  createCostume(100, 100, (ctx) => {
      ctx.fillStyle = 'yellow';
      ctx.beginPath();
      ctx.fillRect(0, 0, 100, 100);
      ctx.fill();
    }, 'yellow square');
}

export function createCostume(w: number, h: number, fn: (ctx: CanvasRenderingContext2D) => void, name?: string) {
  const canvas = document.createElement('canvas');
  canvas.width = w;
  canvas.height = h;

  const ctx = canvas.getContext('2d')!;
  fn(ctx);

  if (name) {
    costumes.set(name, costumeFromElement(canvas));
    return name;
  } else {
    return add(canvas);
  }
}

function costumeFromElementId(id: string) {
  return costumeFromElement(document.getElementById(id) as HTMLImageElement);
}

function costumeFromElement(element: HTMLImageElement | HTMLCanvasElement): Costume {
  const w = element.width;
  const h = element.height;
  const canvas = document.createElement('canvas');
  canvas.width = w;
  canvas.height = h;
  const ctx = canvas.getContext('2d', { willReadFrequently: true })!;
  ctx.drawImage(element, 0, 0);
  return { element, bounds: { x: -w / 2, y: -h / 2, w, h }, canvas, ctx };
}
