import { Trigger } from './types';

export function triggerToString(t: Trigger) {
  return 'hz' in t ? `${t.hz} Hz` : `${t.key} key`;
}

export function triggerFromString(s: String): Trigger | null {
  const parts = s.split(' ');
  if (parts.length !== 2) {
    return null;
  }

  const type = parts[1].toLowerCase();
  switch (type) {
    case 'hz': {
      const value = parseInt(parts[0]);
      return !Number.isNaN(value) ? { hz: value } : null;
    }
    case 'key':
      return { key: parts[0] };
    default:
      return null;
  }
}
